.ng2-c-portfolio-header {
  color: #ffffff;
  display: block;
  flex-shrink: 0; }
  .ng2-c-portfolio-header .ng2-c-header__main {
    opacity: 0;
    transform: translateY(-100%);
    transition: transform 0.3s ease-out; }
  @media (max-width: 767px) {
    .ng2-c-portfolio-header .ng2-c-header--sticky .ng2-c-header__left {
      display: none; } }

.ng2-c-portfolio-header--fixed .ng2-c-header__main {
  opacity: 1;
  transform: translateY(0); }

.ng2-c-portfolio-header__content {
  /*
        Safari has by default a bouncy scroll on pages..
        The problem is that our body has a light background and this element has a blue gradient.
        When pulling down the page on IOS, the light grey background "appears",
        which looks weird because we also have a blue statusbar that is fixed (so it does not move).
        So we can "fix" this by adding a large padding top in combination with a large negative margin-top value..
        So you can still pull down the page but it keeps the blue background..

        This is to fix bug #6489
        #gohacks
    */
  background-image: linear-gradient(to right, #28648a, #004160);
  background-repeat: no-repeat;
  background-size: cover;
  display: flex;
  flex-direction: column;
  height: 134px;
  justify-content: center;
  margin-top: -100vh;
  padding-top: 100vh;
  position: relative;
  text-align: center;
  z-index: 160; }
  @media (min-width: 768px) {
    .ng2-c-portfolio-header__content {
      height: 350px; } }

.ng2-c-portfolio-header-summary {
  align-items: center;
  display: flex;
  font-size: 12px;
  height: 100%;
  justify-content: center;
  text-shadow: 0 1px 0 rgba(0, 0, 0, 0.3);
  width: 100%; }
  @media (min-width: 768px) {
    .ng2-c-portfolio-header-summary {
      font-size: 18px; } }

.ng2-c-portfolio-header-summary__title {
  font-size: 12px; }
  @media (min-width: 768px) {
    .ng2-c-portfolio-header-summary__title {
      font-size: 18px; } }

.ng2-c-portfolio-header-summary__error,
.ng2-c-portfolio-header-summary__name {
  font-size: 18px;
  margin: 0 50px;
  width: auto !important; }
  @media (min-width: 768px) {
    .ng2-c-portfolio-header-summary__error,
    .ng2-c-portfolio-header-summary__name {
      font-size: 40px; } }
  .ng2-c-portfolio-header-summary__error .ng2-o-button,
  .ng2-c-portfolio-header-summary__name .ng2-o-button {
    margin-top: 15px; }
    @media (max-width: 767px) {
      .ng2-c-portfolio-header-summary__error .ng2-o-button,
      .ng2-c-portfolio-header-summary__name .ng2-o-button {
        /* Use small version of button */
        font-size: 14px;
        height: 30px;
        line-height: 30px;
        padding: 0 10px; }
        .ng2-c-portfolio-header-summary__error .ng2-o-button .ng2-o-button__text,
        .ng2-c-portfolio-header-summary__name .ng2-o-button .ng2-o-button__text {
          font-size: 14px;
          line-height: 30px; } }

.ng2-c-portfolio-header-summary__name {
  display: block;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  width: 100%; }

.ng2-c-portfolio-header-summary__error {
  width: 100% !important; }
  .ng2-c-portfolio-header-summary__error p {
    color: #ffffff; }

.ng2-c-portfolio-header__back {
  cursor: pointer;
  line-height: 50px;
  z-index: 1; }

.ng2-c-portfolio-header__back--absolute {
  left: 0;
  position: fixed;
  top: 17px; }
  @media (min-width: 768px) {
    .ng2-c-portfolio-header__back--absolute {
      position: absolute;
      top: 100vh; } }

.ng2-c-portfolio-header__icon {
  align-items: center;
  display: flex; }

.body--is-ios .ng2-c-portfolio-header__back--absolute {
  top: calc(20px + 17px);
  top: calc(constant(safe-area-inset-top) + 17px);
  top: calc(env(safe-area-inset-top) + 17px); }

.body--is-ios .ng2-c-portfolio-header {
  margin-top: 20px;
  margin-top: constant(safe-area-inset-top);
  margin-top: env(safe-area-inset-top); }

.body--is-ios .ng2-c-portfolio-header {
  position: relative; }

.ng2-c-portfolio-header-summary__content {
  width: 100%; }

.ng2-c-portfolio-header-summary__iban {
  font-size: 12px;
  margin-bottom: 20px; }
  @media (min-width: 768px) {
    .ng2-c-portfolio-header-summary__iban {
      font-size: 18px; } }

.ng2-c-portfolio-header-summary__total {
  align-items: center;
  display: flex;
  font-size: 12px;
  justify-content: center;
  margin: 0 auto 20px; }
  .ng2-c-portfolio-header-summary--multi .ng2-c-portfolio-header-summary__total {
    margin-bottom: 0; }
  .ng2-c-portfolio-header-summary__total .ng2-c-portfolio-header__amount,
  .ng2-c-portfolio-header-summary__total .ng2-c-portfolio-header__stocks {
    flex-shrink: 0;
    line-height: 1.1; }

.ng2-c-portfolio-header__amount {
  font-size: 32px; }
  @media (min-width: 414px) {
    .ng2-c-portfolio-header__amount {
      font-size: 40px; } }
  @media (min-width: 768px) {
    .ng2-c-portfolio-header__amount {
      font-size: 78px; } }

.ng2-c-portfolio-header__stocks {
  font-size: 14px;
  padding-left: 10px;
  text-align: left; }
  @media (min-width: 414px) {
    .ng2-c-portfolio-header__stocks {
      font-size: 16px;
      padding-left: 20px; } }
  @media (min-width: 768px) {
    .ng2-c-portfolio-header__stocks {
      font-size: 46px; } }

.ng2-c-portfolio-header__since {
  font-size: 10px;
  white-space: nowrap; }
  @media (min-width: 414px) {
    .ng2-c-portfolio-header__since {
      font-size: 12px; } }

.ng2-c-portfolio-header__contracts {
  display: none; }
  @media (min-width: 768px) {
    .ng2-c-portfolio-header__contracts {
      display: flex;
      justify-content: center;
      margin: 0 auto;
      padding-top: 50px;
      width: 95%; } }

.ng2-c-portfolio-header__contracts--2 .ng2-c-portfolio-header__contracts-item {
  width: 50%; }

.ng2-c-portfolio-header__contracts--3 .ng2-c-portfolio-header__contracts-item {
  width: 33.33%; }

.ng2-c-portfolio-header__contracts-item {
  border-right: 1px solid rgba(255, 255, 255, 0.2);
  box-sizing: border-box;
  padding: 0 10px;
  width: 100%; }
  .ng2-c-portfolio-header__contracts-item:last-child {
    border-right: 0; }

.ng2-c-portfolio-header__label--stock {
  font-size: 22px; }

.ng2-c-portfolio-header__label--name {
  display: block;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  width: 100%;
  font-size: 18px; }

.ng2-c-portfolio-header__label--iban {
  font-size: 16px; }
